.stars {
  display: flex;

  &__star {
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;

    @include star (16px);
    margin-left: 4px;

    &:first-child {
      margin-left: 0;
    }
  }

  @for $i from 1 through 5 {

    &--#{$i} > .stars__star:nth-child(-n + #{$i}) {
        background-image: url("../images/star-active.svg");
    }
  }
}
